<template>
	<div class="log">
    <el-card class="search-wrapper">
      <el-form :model="searchForm" :size="size" ref="searchForm" class="search-form" label-width="100px">
        <el-form-item class="search-form-item" label="用户名" prop="userName">
          <el-input v-model="searchForm.userName"></el-input>
        </el-form-item>
        <el-form-item class="search-form-item" label="用户角色" prop="userRole">
          <el-input v-model="searchForm.userRole"></el-input>
        </el-form-item>
        <el-form-item class="search-form-item" label="分厂" prop="factory">
          <el-input v-model="searchForm.factory" :disabled="(auth === 'factorier')"></el-input>
        </el-form-item>
      </el-form>
      <div class="button-wrapper">
        <el-button :size="size" type="primary" @click="search('searchForm')">搜索</el-button>
        <el-button :size="size" @click="resetForm('searchForm')">重置</el-button>
      </div>
    </el-card>
    <el-card class="table-wrapper" ref="tableWrapper">
      <el-table :size="size"
                :data="logData"
                :height="tableHeight"
                style="width: 100%" class="table-list" border>
        <el-table-column prop="opUserId" label="用户id" header-align="center"></el-table-column>
        <el-table-column prop="opName" label="用户名" header-align="center"></el-table-column>
        <el-table-column prop="opRole" width="100" label="用户角色" header-align="center"></el-table-column>
        <el-table-column prop="opFactory" label="分厂" header-align="center"></el-table-column>
        <el-table-column prop="opBehavier" width="140" label="操作信息" header-align="center"></el-table-column>
        <el-table-column prop="opAmount" label="数量" header-align="center"></el-table-column>
        <el-table-column prop="opAddress" label="外借地点" header-align="center"></el-table-column>
        <el-table-column prop="opTime" width="100" label="操作时间" header-align="center" :formatter="formatterColumn"></el-table-column>
      </el-table>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="logAmount"
        :small="sizesmall"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
  import * as api from 'api/index'
  import moment from 'moment'
  import * as util from 'assets/js/util'
  import { mapGetters, mapMutations } from 'vuex'

  let myMixin = {
    data: function () {
      return { api }
    }
  }
  
  export default {
    name: "Log",
    mixins: [myMixin],
    data() {
      return {
        size: 'medium',
        sizesmall: false,
        currentPage: 1,
        pageSize: 10,
        logData: [],
        logAmount: '',
        searchForm: {
          userName: '',
          userRole: '',
          factory: ''
        }
      }
    },
    methods: {
      // 调整日期格式
      formatterColumn(row, column) {
        var date = row[column.property]
        if (date == undefined) {
          return ""
        }
        return moment(date).format("YYYY-MM-DD HH:mm:ss")
      },
      //表单方法
      search(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let factory = (this.factory !== '') ? this.factory : this.searchForm.templateFactory
            let form = {
              userName: this.searchForm.userName,
              userRole: this.searchForm.userRole,
              factory: factory
            }
            api.GetOrderList.r(form, 1).then(({ data }) => {
              this.$router.push(`/log?userName=${this.searchForm.userName}&userRole=${this.searchForm.userRole}&factory=${factory}&currentPage=1`)
              this.currentPage = 1
              this.logData = data.data.orderList
              this.logAmount = parseInt(data.data.total)
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      // 分页器方法
      handleCurrentChange(currentPage) {
        this.currentPage = parseInt(currentPage)
        let factory = (this.auth === 'factorier') ? this.factory : this.searchForm.templateFactory
        let form = {
          userName: this.searchForm.userName,
          userRole: this.searchForm.userRole,
          factory: factory
        }
        api.GetOrderList.r(form, this.currentPage).then(({ data }) => {
          this.logData = data.data.orderList
          this.logAmount = parseInt(data.data.total)
        })
        this.$router.push(`/log?userName=${this.searchForm.userName}&userRole=${this.searchForm.userRole}&factory=${factory}&currentPage=${currentPage}`)
      }
    },
    computed: {
      ...mapGetters([ 'factory', 'auth' ])
    },
    created() {
      this.size = (window.innerWidth < 767) ? 'mini': 'medium'
      this.sizesmall = (window.innerWidth < 767) ? true : false
      
      this.currentPage = parseInt(this.$route.query.currentPage) || 1
      let factory = (this.auth === 'factorier') ? this.factory : this.$route.query.factory
      this.searchForm.userName = this.$route.query.userName || ''
      this.searchForm.userRole = this.$route.query.userRole || ''
      console.log(factory)
      let form = {
        userName: this.searchForm.userName,
        userRole: this.searchForm.userRole,
        factory: factory
      }
      let currentPage = this.currentPage
      api.GetOrderList.r(form, this.currentPage).then(({ data }) => {
        if (data.success === 'SUCCESS') {
          this.logData = data.data.orderList
          this.logAmount = parseInt(data.data.total)
        }
      })
      this.$router.push(`/log?userName=${this.searchForm.userName}&userRole=${this.searchForm.userRole}&factory=${factory}&currentPage=1`)
    },
    mounted() {
      if (window.innerWidth >= 767) {
        let twHeight = this.$refs.tableWrapper.$el.clientHeight
        this.tableHeight = twHeight - 50
        console.log(this.tableHeight)
      }
    }
  }
</script>

<style type="text/stylus" lang="stylus" rel="stylesheet/stylus" scoped>
  @import "~assets/stylus/variable.styl"
  @import "Log.styl"
</style>



